import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../data/api.service';
import { ActivatedRoute } from '@angular/router';
import { AuthService } from '../../auth/auth.service';
import { ConfirmationService } from 'primeng/api';
import { NotificationsService } from '../../common/notifications/notifications.service';

@Component({
  selector: 'app-modality-filters',
  templateUrl: './modality-filters.component.html',
  styleUrls: ['./modality-filters.component.css'],
  host: {
    'class': 'col-sm-8'
  }
})
export class ModalityFiltersComponent implements OnInit {

  displayEventModal: boolean = false;
  currentEntity: any = {};
  currentEvents: Array<any> = [];
  modalityId: number;

  constructor(private apiService: ApiService, private route: ActivatedRoute, private authService: AuthService,
    private confirmationService: ConfirmationService, private notificationsService: NotificationsService) { }

  ngOnInit() {
    this.modalityId = this.route.snapshot.params.id;
  }

  entitySelected(item) {
    this.currentEntity = item;

    this.apiService.setEntity(`/sports/modalities/${this.modalityId}/event_categories`);
    this.apiService.getAll().subscribe(data => {
      this.apiService.setEntity(`/users/${this.authService.user.ID}/subscriptions/${this.currentEntity.ID}/events`);
      this.apiService.getAll().subscribe(myData => {
        this.currentEvents = data.map(dt => Object.assign({}, dt, { message: '', subscribed: myData.filter(mdt => mdt.ID === dt.ID).length !== 0 }));
        this.currentEvents = this.currentEvents.sort((a, b) => a.name > b.name ? 1 : 0);
        this.displayEventModal = true;
      });
    });
  }

  subscribeEvent(event) {
    const cost = this.currentEntity.isMatch ? event.regularPrice : event.extraPrice;
    this.confirmationService.confirm({
      message: `Are you sure that you want to subscribe to '${event.name}' of ${this.currentEntity.name}? It will cost you ${cost}.`,
      key: 'eventsConfirmation',
      accept: () => {
        this.apiService.setEntity(`/users/${this.authService.user.ID}/subscribe/${this.currentEntity.ID}`);
        const body = {
          date: new Date(),
          paid: false,
          _tracker: {
            notificationPolicy: 'default'
          },
          ORM_subscribedEvents: [
            { ID: event.ID }
          ]
        }
        this.apiService.postRequest(body).subscribe(data => {
          event.subscribed = true;
          this.authService.updateBalance();
        });
      }
    });
  }

  onKey(event, item) {
    item.message = (<HTMLInputElement>event.target).value;
  }

  sendNotification(item) {
    this.confirmationService.confirm({
      message: `Are you sure that you want to send a notification to all subscribers of '${item.name}' of ${this.currentEntity.name}.`,
      key: 'sendNotificationConfirmation',
      accept: () => {
        const event = {
          category: {
            ID: item.ID
          },
          textFormat: item.message
        };

        this.apiService.setEntity(`sports/modalities/competitions/matches/${this.currentEntity.ID}/events`);
        this.apiService.postRequest(event).subscribe(data => {
          this.notificationsService.notify('success', 'Notification sent', `Notification sucessfully sent to all subscribers of '${item.name}' of ${this.currentEntity.name}.`);
        });
      }
    });
  }

}