import { Component, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from '../../data/api.service';
import { NgbTabset } from '@ng-bootstrap/ng-bootstrap';
import { ActivatedRoute, Router } from '@angular/router';

import { Globals } from '../../common/globals';
import { NotificationsService } from '../../common/notifications/notifications.service';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.css']
})
export class SignUpComponent implements OnInit {

  protected form: FormGroup;
  protected tabSet: NgbTabset;

  errorMessage: String = null;

  constructor(private globals: Globals, private fb: FormBuilder, private router: Router, private apiService: ApiService,
    private notificationsService: NotificationsService) { }

  @ViewChild(NgbTabset) set content(content: NgbTabset) {
    this.tabSet = content;
  };

  ngOnInit() {
    this.form = this.fb.group({
      Name: [null, Validators.required],
      Email: [null, Validators.required],
      Password: [null, Validators.required],
      PasswordR: [null, Validators.required],
      // PaymentType: [null, Validators.required]
    });
  }

  signUpAccount() {
    this.errorMessage = null;
    if (!this.form.valid) {
      this.errorMessage = "All fields must be filled";
      return;
    } else if (this.form.value.Password !== this.form.value.PasswordR) {
      this.errorMessage = "Passwords must match";
      return;
    } else if (this.tabSet.activeId === "PrepaidCard") {
      this.errorMessage = "ERROR: Payment method not available, yet!";
      return;
    }
    else {

      const user = {
        "email": this.form.value.Email,
        "name": this.form.value.Name,
        "password": this.form.value.Password,
        "paymentManager": {
          "currentAmount": 0,
          "type": this.tabSet.activeId
        }
      }
      this.apiService.setEntity('users/register');
      this.apiService.postRequest(user).subscribe(data => {
        if (data === -1) {
          this.notificationsService.notify('error', 'Unvalid information', 'User\'s email already exists.');
        } else {
          this.notificationsService.notify('success', 'Sucess', 'You just have successfully created a new account.');
          this.router.navigate(['/welcome']);
        }
      });
    }
  }

}
