import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { User } from './user';
import { ApiService } from '../data/api.service';
import { NotificationsService } from '../common/notifications/notifications.service';


@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private loggedIn = new BehaviorSubject<boolean>(false);
  public user: User;

  get isLoggedIn() {
    return this.loggedIn.asObservable();
  }

  constructor(private router: Router, private apiService: ApiService, private notificationsService: NotificationsService) {
    // TODO: remove
    this.user = {
      ID: 1,
      email: 'email@gmail.com',
      name: 'Miguel Calafate',
      isManager: true,
      paymentManager: {
        currentAmount: 0
      }
    };
  }

  login(user) {
    const user_request = {
      'email': user.Email,
      'password': user.Password
    }

    this.apiService.setEntity('users/login');
    this.apiService.postRequest(user_request).subscribe(data => {
      if (data !== undefined) {
        this.loggedIn.next(true);
        this.router.navigate(['/sports']);
        this.user = data;
      } else {
        this.notificationsService.notify('error', 'Invalid user', 'The email/password pair doesn\'t exists.');
      }
    });
  }

  logout() {
    this.loggedIn.next(false);
    this.router.navigate(['/welcome']);
  }

  updateBalance() {
    this.apiService.setEntity(`users/${this.user.paymentManager.ID}`);
    this.apiService.getAll().subscribe(data => {
      this.user.paymentManager.currentAmount = data.paymentManager.currentAmount;
    })
  }
}