import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'app-array-paginator',
  templateUrl: './array-paginator.component.html',
  styleUrls: ['./array-paginator.component.css']
})
export class ArrayPaginatorComponent implements OnInit {

  @Output() dataPaginating = new EventEmitter<any>();
  @Input() numberOfRows = 8;
  private _data: Array<any>;
  @Input() set data(data: Array<any>) {
    // if (data === undefined || data === null || data === []) {
    // }
    this._data = data || [];
    // this.dataPaginating.emit(this._data.slice(0, this.numberOfRows));
    this.paginate({ first: 0, rows: 8 });
  }
  
  constructor() { }

  ngOnInit() {
  }

  paginate(page) {
    this.dataPaginating.emit({
      from: page.first,
      to: page.rows + page.first
    });
    // this.dataPaginating.emit(this._data.slice(page.first, page.rows + page.first));
}

}
