import { Injectable } from '@angular/core';
import { Http, Response, Headers, ResponseContentType, RequestOptions } from '@angular/http';

import { Observable, Subject, ReplaySubject, from, of, range } from 'rxjs';
import { map, filter, switchMap } from 'rxjs/operators';

import { Globals } from '../common/globals';
import { debug } from 'util';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  /**
  * Entity
  */
  entity: string;

  constructor(private http: Http, private globals: Globals) { }

  /**
   * Set entity
   * @param entity
   */
  public setEntity(entity: string) {
    this.entity = entity;
  }

  /**
   * Get all
   */
  public getAll(): Observable<any> {
    if (this.entity !== null && this.entity !== undefined) {
      return this.http
        .get(this.globals.API_URL + '/' + this.entity)
        .pipe(map(response => this.convertToJson(response)));
    } else {
      const error = 'Missing entity';
      return Observable.throw(error);
    }
  }

  /**
   * Post an item
   * @param item
   */
  public postRequest(item: any): Observable<any> {
    return this.http
      .post(this.globals.API_URL + '/' + this.entity, item)
      .pipe(map(response => this.convertToJson(response)));
  }

  private convertToJson(object) {
    try {
      return object.json();
    } catch {
      return undefined;
    }
  }

  /**
   * Handle error
   * @param error
   */
  private handleError(error: Response | any) {
    console.error('ApiService::handleError', error, this.entity);
    return Observable.throw(error);
  }
}
