import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from '../../data/api.service';

@Component({
  selector: 'app-modalities',
  templateUrl: './modalities.component.html',
  styleUrls: ['./modalities.component.css'],
  host: {
    'class': 'col-sm-8'
  }
})
export class ModalitiesComponent implements OnInit {

  sport: any = {
    modalities: []
  };
  data: Array<any>;
  ordersBy: Array<any>;
  dataFrom: number = 0;
  dataTo: number = 8;

  constructor(private route: ActivatedRoute, private apiService: ApiService) { }

  ngOnInit() {
    this.getData();

    this.ordersBy = [
      { value: 'name', name: 'Name' }
    ]
  }

  getData() {
    const sportId = this.route.snapshot.params.id;
    this.apiService.setEntity(`sports/${sportId}/modalities`);
    this.apiService.getAll().subscribe(data => {
      this.sport = {
        Id: sportId,
        Designation: null,
        modalities: data
      }
      this.data = data;
    })
  }

  filtering(data: Array<any>) {
    this.data = data;
  }

  paginating(values) {
    this.dataFrom = values.from;
    this.dataTo = values.to;
  }
}
