import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from '../../data/api.service';

@Component({
  selector: 'app-competitions',
  templateUrl: './competitions.component.html',
  styleUrls: ['./competitions.component.css']
})
export class CompetitionsComponent implements OnInit {

  @Output() onItemSelected = new EventEmitter<number>();

  competitions: Array<any>;
  matches: Array<any>;
  data: Array<any>;
  ordersBy: Array<any>;
  dataFrom: number = 0;
  dataTo: number = 2;

  pickedCompetition: any = {};
  displayCompetitionModal: Boolean = false;

  constructor(private route: ActivatedRoute, private apiService: ApiService) { }

  ngOnInit() {
    this.getCompetitions();

    this.ordersBy = [
      { value: 'name', name: 'Name' },
      { value: 'location', name: 'Location' },
      { value: 'startDate', name: 'Start Date' },
    ]
  }

  getCompetitions() {
    const teamId = this.route.snapshot.params.id;
    this.apiService.setEntity(`/sports/modalities/${teamId}/competitions`);
    this.apiService.getAll().subscribe(data => {
      // debugger
      this.competitions = data;
      this.data = data;
    })
  }

  filtering(data: Array<any>) {
    this.data = data;
  }

  paginating(values) {
    this.dataFrom = values.from;
    this.dataTo = values.to;
  }

  selectItem(item) {
    this.displayCompetitionModal = false;
    this.onItemSelected.emit(Object.assign({}, item, { isMatch: true, name: item.description }));
  }

  selectCompetition(competition) {
    this.apiService.setEntity(`sports/modalities/competitions/${competition.ID}/matches`);
    this.apiService.getAll().subscribe(data => {
      this.matches = data;
      this.pickedCompetition = competition;
      this.displayCompetitionModal = true;
    });
  }
}
