import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-array-filter',
  templateUrl: './array-filter.component.html',
  styleUrls: ['./array-filter.component.css']
})
export class ArrayFilterComponent implements OnInit {

  @Input() ordersBy: Array<any>;
  @Input() dataFilter: string;
  @Output() dataFiltering = new EventEmitter<Array<any>>();

  private _data: Array<any>;
  @Input() set data(data: Array<any>) {
    this.results = data;
    this._data = data;
  }

  isAscending: Boolean = true;

  results: Array<any>;

  constructor() { }

  ngOnInit() {
  }

  search(event) {
    const query = event.query || event[this.dataFilter];
    this.results = this._data.filter(d => query === undefined || d[this.dataFilter].toLowerCase().indexOf(query.toLowerCase()) == 0)
    this.dataFiltering.emit(this.results);
  }

  orderSelected(value) {
    this.results = this.results.sort((a, b) => a[value] > b[value] ? 1 : 0);
    if (!this.isAscending) {
      this.results = this.results.reverse();
    }
    this.dataFiltering.emit(this.results);
  }

  changeOrder() {
    this.isAscending = !this.isAscending;
    this.results = this.results.reverse();
    this.dataFiltering.emit(this.results);
  }

}
