import { Component, OnInit } from '@angular/core';

import { Globals } from '../../common/globals';
import { AuthService } from '../../auth/auth.service';
import { PusherService } from '../../common/pusher.service';
import { NotificationsService } from '../../common/notifications/notifications.service';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ApiService } from '../../data/api.service';


@Component({
  selector: 'app-logged-navbar',
  templateUrl: './logged-navbar.component.html',
  styleUrls: ['./logged-navbar.component.css']
})
export class LoggedNavbarComponent implements OnInit {

  messageChannel: any;

  paths: Array<any>;

  constructor(private globals: Globals, private authService: AuthService, private pusher: PusherService,
    private notificationsService: NotificationsService, private router: Router, private route: ActivatedRoute,
    private apiService: ApiService) {

  }

  ngOnInit() {
    this.setPusherData();
    this.generatePaths();
  }

  logout() {
    this.authService.logout();
  }

  setPusherData() {
    const id = this.authService.user.ID
    this.messageChannel = this.pusher.pusher.subscribe(id.toString())
    this.messageChannel.bind('my-event', (message) => {
      this.notificationsService.notify('info', message.title, message.content);
    })
  }

  generatePaths() {
    this.router.events.subscribe(val => {
      if (val instanceof NavigationEnd) {
        const url = val.url;
        if (url === '/sports') {
          this.paths = [{ classes: 'active', path: '/sports', designation: 'Sports', last: true }];
        } else {
          this.apiService.setEntity('sports');
          const sportId = /^\/sport\/([0-9]+)/g.exec(url)[1];
          if (url.search(/^\/sport\/[0-9]+\/modalities$/g) != -1) {
            const body = {
              id: sportId,
              isSport: true,
              modalityID: 1
            };
            this.apiService.postRequest(body).subscribe(data => {
              console.log("recebi")
              console.log(data);
              this.paths = [{ classes: '', path: '/sports', designation: data.sportName, last: false },
              { classes: 'active', path: url, designation: 'Modalities', last: true }];
            })
          } else if (url.search(/^\/sport\/[0-9]+\/modality\/[0-9]+\/filters$/g) != -1) {
            const id = /^\/sport\/[0-9]+\/modality\/([0-9]+)\/filters$/g.exec(url)[1];
            const body = {
              id: sportId,
              isSport: false,
              modalityID: id
            };
            this.apiService.postRequest(body).subscribe(data => {
              console.log("recebi2")
              console.log(data);
              if (data.hasModality) {
                this.paths = [{ classes: '', path: '/sports', designation: data.sportName, last: false },
                { classes: '', path: `/sport/${sportId}/modalities`, designation: data.modalityName, last: false },
                { classes: 'active', path: url, designation: 'Filters', last: true }];
              } else {
                this.paths = [{ classes: '', path: '/sports', designation: data.sportName, last: false },
                { classes: 'active', path: url, designation: 'Filters', last: true }];
              }
            })
          }
        }
      }
    });
  }
}
