import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../data/api.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-sports',
  templateUrl: './sports.component.html',
  styleUrls: ['./sports.component.css'],
  host: {
    'class': 'col-sm-8'
  }
})
export class SportsComponent implements OnInit {

  sports: Array<any>;
  data: Array<any>;
  ordersBy: Array<any>;
  dataFrom: number = 0;
  dataTo: number = 8;


  constructor(private route: ActivatedRoute, private router: Router, private apiService: ApiService) {}

  ngOnInit() {
    this.getSports();

    this.ordersBy = [
      { value: 'name', name: 'Name' }
    ]
  }

  getSports() {
    this.apiService.setEntity('sports');
    this.apiService.getAll().subscribe(data => {
      this.sports = data;
      this.data = data;
    });
  }

  filtering(data: Array<any>) {
    this.data = data;
  }

  sportClick(sport) {
    if (sport.onlySport) {
      this.router.navigate([`/sport/${sport.ID}/modalities`]);
    } else {
      this.apiService.setEntity(`sports/${sport.ID}/modalities`);
      this.apiService.getAll().subscribe(data => {
        this.router.navigate([`/sport/${sport.ID}/modality/${data[0].ID}/filters`]);
      });
    }
  }

  paginating(values) {
    this.dataFrom = values.from;
    this.dataTo = values.to;
  }

}
