import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ApiService } from '../../data/api.service';

@Component({
  selector: 'app-teams',
  templateUrl: './teams.component.html',
  styleUrls: ['./teams.component.css']
})
export class TeamsComponent implements OnInit {

  @Output() onItemSelected = new EventEmitter<number>();

  teams: Array<any>;
  data: Array<any>;
  ordersBy: Array<any>;
  dataFrom: number = 0;
  dataTo: number = 8;

  constructor(private route: ActivatedRoute, private apiService: ApiService) { }

  ngOnInit() {
    this.getTeams();

    this.ordersBy = [
      { value: 'name', name: 'Name' }
    ]
  }

  getTeams() {
    const teamId = this.route.snapshot.params.id;
    this.apiService.setEntity(`/sports/modalities/${teamId}/teams`);
    this.apiService.getAll().subscribe(data => {
      this.teams = data;
      this.data = data;
    })
  }

  filtering(data: Array<any>) {
    this.data = data;
  }

  selectItem(item) {
    this.onItemSelected.emit(Object.assign({}, item, { isMatch: false }));
  }

  paginating(values) {
    this.dataFrom = values.from;
    this.dataTo = values.to;
  }

}
