import { NgModule } from '@angular/core';
import { RouterModule, Routes } from "@angular/router";

import { AuthGuard } from '../../auth/auth.guard';

import { WelcomeComponent } from "../../unlogged/welcome/welcome.component";
import { SignUpComponent } from "../../unlogged/sign-up/sign-up.component";
import { SportsComponent } from '../../logged/sports/sports.component';
import { ModalitiesComponent } from "../../logged/modalities/modalities.component";
import { ModalityFiltersComponent } from '../../logged/modality-filters/modality-filters.component';


const routes: Routes = [
  { path: '', redirectTo: '/welcome', pathMatch: 'full' },
  { path: 'welcome', component: WelcomeComponent },
  { path: 'signup', component: SignUpComponent },
  // { path: 'sports', component: SportsComponent, canActivate: [AuthGuard] },
  { path: 'sports', component: SportsComponent },
  { path: 'sport/:id/modalities', component: ModalitiesComponent },
  { path: 'sport/:sportId/modality/:id/filters', component: ModalityFiltersComponent },
  { path: '**', redirectTo: '/sports', canActivate: [AuthGuard] },
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule { }
